<template>
  <div>
    <div id="topBox">
      <img
        src="../../assets/login/warning.png"
        alt
      />
      <div style="font-size:0.7rem;">
        <p>1、断网后修改管理密码无法立即生效，需要网络正常才能使用。</p>
        <p>2、管理密码在有效期内可用，超过有效期无法开门。</p>
        <p>3、请谨慎保管，切勿泄露！定期修改！</p>
      </div>
    </div>
    <div id="pwBox">
      <p class="top_title">密码列表</p>
      <ul
        class="list"
        style="margin-top:10px;"
        v-if="lockInfo"
      >
        <li
          class="item"
          style="padding:4px 0;"
        >
          <div class="div_img">
            <img
              style="display:inline-block;width:14px;height:14px;margin:auto 0;margin-left:10px;"
              src="../../assets/yes.png"
              @click="dele"
              alt
            />
            <div
              class="ps_info"
              @click="editPs('edit')"
            >
              <!-- <div> -->
              <p class="weight">{{ password }}</p>
              <p class="c_b2">{{ startTime }}至{{ endTime }}</p>
              <!-- </div> -->
            </div>
          </div>
          <img
            @click="editPs('edit')"
            class="li_img"
            src="../../assets/right.png"
            alt
          />
          <!-- </div> -->
        </li>
      </ul>
      <div
        v-if="lockInfo == null"
        class="bototm_title"
        @click="addPs('add')"
      >添加管理密码</div>
    </div>
    <!-- 添加管理密码 -->
    <van-popup
      position="center"
      id="add_show_box"
      v-model="add_show"
    >
      <p style="font-size:16px;padding:15px;text-align:center;">密码设置</p>
      <van-field
        maxlength='8'
        v-model="psword"
        label="输入密码"
        placeholder="请输入8位数密码"
      />
      <van-field
        v-model="date"
        readonly
        label="有效日期"
        placeholder="点击选择有限日期"
        @click.native="getTime"
      />
      <div class="buttons">
        <div
          class="canle"
          @click="add_show = false"
        >取消</div>
        <div
          class="sure"
          @click="sure"
        >确定</div>
      </div>
    </van-popup>
    <!-- 选择时间 -->
    <van-calendar
      v-model="time_show"
      type="range"
      :min-date="minDate"
      :max-date="maxDate"
      @confirm="onConfirm"
    />
  </div>
</template>
<script>
import { Toast, MessageBox } from 'mint-ui';
import { PostBusinessHotelLockAdd, PostBusinessHotelLockEdit, PostBusinessHotelLockByhotel, PostBusinessHotelLockRemove } from '@/api/api_business_login';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            add_show: false,
            time_show: false,
            psword: '',
            minDate: new Date(), //设置过期时间为当天
            maxDate: new Date(),
            date: '',
            doble: [],
            lockInfo: null,
            password: null,
            startTime: null,
            endTime: null
        };
    },
    created() {
        this.$store.commit('setHeader', '管理员密码');
        var dayjs = require('dayjs');
        this.maxDate = new Date(dayjs(this.minDate).add(2, 'month'));
        this.getPSinfo();
    },
    methods: {
        addPs(type) {
            this.type = type;
            this.psword = '';
            this.date = '';
            this.add_show = true;
        },
        // 点击选择日期
        getTime() {
            this.time_show = true;
            this.doble = [];
        },
        // 选择时间-1
        onConfirm(date) {
            const [start, end] = date;
            this.time_show = false;
            this.date = `${this.formatDate(start)}至${this.formatDate(end)}`;
        },
        // 选择时间-2
        formatDate(date) {
            this.add_show = true;
            return `${date.getFullYear()}-${date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1}-${date.getDate() < 10 ? '0' + date.getDate() : date.getDate()}`;
        },
        // 确定
        sure() {
            if (!this.psword || this.psword.length != 8) {
                Toast({
                    message: '请正确输入8位数密码',
                    duration: 900
                });
                this.psword = '';
                return false;
            }
            if (!this.date) {
                Toast({
                    message: '请选择有效日期',
                    duration: 900
                });
                return false;
            }
            if (dayjs(this.date.substr(11, 10)).unix() < dayjs().unix()) {
                Toast({
                    message: '请重新选择有效日期',
                    duration: 900
                });
                return false;
            }
            if (this.type == 'add') {
                let data = {
                    hotelId: this.$route.query.hotelId,
                    password: this.psword,
                    startTime: this.date.substr(0, 10) + ' ' + '00:00:00',
                    endTime: this.date.substr(11, 10) + ' ' + '00:00:00'
                };
                PostBusinessHotelLockAdd(data).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 900
                    });
                    this.getPSinfo();
                    this.add_show = false;
                });
            } else {
                let data = {
                    id: this.lockInfo.id,
                    password: this.psword,
                    startTime: this.date.substr(0, 10) + ' ' + '00:00:00',
                    endTime: this.date.substr(11, 10) + ' ' + '00:00:00'
                };
                PostBusinessHotelLockEdit(data).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 900
                    });
                    this.getPSinfo();
                    this.add_show = false;
                });
            }
        },
        // 获取密码列表
        getPSinfo() {
            PostBusinessHotelLockByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                this.lockInfo = res.data;
                if (res.data) {
                    this.password = res.data.password;
                    this.startTime = res.data.startTime;
                    this.endTime = res.data.endTime;
                }
            });
        },
        // 编辑
        editPs(type) {
            this.type = type;
            this.add_show = true;
            this.psword = this.lockInfo.password;
            this.date = `${this.lockInfo.startTime.substr(0, 10)}至${this.lockInfo.endTime.substr(0, 10)}`;
        },
        // 删除
        dele() {
            MessageBox.confirm('', {
                message: `密码：${this.lockInfo.password}`,
                title: '确认删除该管理密码？',
                cancelButtonText: '取消',
                confirmButtonText: '确定'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        PostBusinessHotelLockRemove({ hotelLockId: this.lockInfo.id }).done((res) => {
                            this.getPSinfo();
                        });
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        }
    }
};
</script>
<style scoped>
.weight {
    font-weight: bold;
    color: #000;
}
.c_b2 {
    color: #b2b2b2;
}
#topBox {
    display: flex;
    font-size: 0.8rem;
    color: #989898;
    justify-content: center;
    align-items: center;
    background: #fff;
    margin: 10px 0 30px;
    padding: 10px;
}
#topBox > img {
    width: 20px;
    height: 20px;
    padding-right: 10px;
}
#topBox > div {
    text-align: left;
}
#pwBox {
    padding: 4px 10px;
    background: #fff;
}
#pwBox > .top_title {
    font-size: 0.9rem;
    font-weight: bold;
    text-align: left;
    color: #000;
}
#pwBox > .bototm_title {
    color: coral;
    font-size: 0.9rem;
    text-align: left;
    padding: 10px;
    padding-left: 10%;
}
#pwBox > .list > .item {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
#pwBox > .list > .item > .div_img {
    display: flex;
}
#pwBox > .list > .item > .div_img > img {
    padding-right: 10px;
}
#pwBox > .list > .item > .div_img > .ps_info {
    width: 100%;
    text-align: left;
    font-size: 0.7rem;
}
#pwBox > .list > .item > .li_img {
    width: 20px;
    height: 20px;
}
#add_show_box {
    width: 80%;
    border-radius: 10px;
    color: #000;
}
#add_show_box > .buttons {
    width: 100%;
    display: flex;
}
#add_show_box > .buttons > div {
    border: 1px solid #e5e5e5;
    width: 50%;
    line-height: 40px;
    font-size: 0.8rem;
}
#add_show_box > .buttons > .sure {
    border-bottom-right-radius: 10px;
    color: #ff7d41;
}
#add_show_box > .buttons > .canle {
    border-bottom-left-radius: 10px;
}
</style>



